<template>
    <div>
        <cjcNav @customRouter="customRouter" title="课程详情"></cjcNav>
        <div class="title" v-if="State.DetailSyllabus">
            {{State.DetailSyllabus[0].KCMC || ''}}
        </div>
        <div class="dearea" v-for="item in State.DetailSyllabus" :key="item.KCH + item.SKXQ">
            <div>
                <listChild
                    icon=" "
                    leftText="教室"
                    rightColor="#999999"
                    :rightText="item.SKJS">
                </listChild>
                <listChild
                    icon=" "
                    leftText="周数"
                    rightColor="#999999"
                    :rightText="item.ZC + '  周'">
                </listChild>
                <listChild
                    icon=" "
                    leftText="节数"
                    rightColor="#999999"
                    :rightText="item.SKXQ + '   ' + item.SKJC">
                </listChild>
                <listChild
                    icon=" "
                    leftText="授课教师"
                    rightColor="#999999"
                    :rightText="item.JSXM">
                </listChild>
            </div>
        </div>
        <Spin size="large" style="height: 100%" fix v-if="spinShow"></Spin>
    </div>
</template>

<script>
export default {
    name: 'details',
    components: {
        cjcNav: () => import('../components/common/Nav.vue'),
        listChild: () => import('../components/listChild.vue'),
        Spin: () => import('iview/src/components/spin')
    },
    data() {
        return {
            spinShow: true,
            State: this.$store.state
        }
    },
    methods: {
        customRouter() {
            this.$router.push({
                name: 'home',
                params: this.$route.params
            })
        }
    },
    created() {
        const Vm = this
        const State = Vm.$store.state
        /**
         * 设置当前校历周
         */
        async function setSchoolTime() {
            let TimeList = await Vm.$store.dispatch('getSchoolTime')
            Vm.$store.commit('SET_SchoolTime', TimeList.result)
            Vm.ZC = State.SchoolTime.ZC
        }
        /**
         * 设置课程详细信息
         */
        async function setDetailSyllabus() {
            let KCH = sessionStorage.getItem('KCH')
            let DetailSyllabus = await Vm.$store.dispatch('getDetailSyllabus', {
                xh: State.userInfo.XH,
                xn: State.SchoolTime.XN,
                xq: State.SchoolTime.XQ,
                kch: KCH
            })
            Vm.$store.commit('SET_DetailSyllabus', DetailSyllabus.result)
            Vm.spinShow = false
        }
        /**
         * 执行顺序
         */
        async function step() {
            await setSchoolTime()
            await setDetailSyllabus()
        }
        step()
    }
}
</script>

<style lang="less" scoped>
    .title {
        height: 50px;
        line-height: 50px;
        background-color: #fff;
        color: #2d8cf0;
        font-size: 14px;
        text-align: center;
    }
    .dearea{
        margin-top: 10px;
        background-color: #fff; 
    }
</style>




